.sidebar-container {
    width: 250px;
    transition: width 0.2s linear;
}

.sidebar-container.reduced {
    width: 80px;
}

.main-container {
    width: calc(100% - 250px);
}

.main-container.reduced {
    width: calc(100% - 80px);
}

.container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}